"use client";

import { Search } from "lucide-react";
import ProductListSkeleton from "../loader/ProductListSkeleton";
import BreadCrumb from "../breadcrumb/BreadCrumb";
import ProductListEmpty from "./ProductListEmpty";
import { useEffect, useState } from "react";
import { GetAllProducts } from "@/libs/api";
import toast from "react-hot-toast";
import ProductRow from "./ProductRow";
import { Product, ProductResponse } from "@/types/auth";
import { useRouter } from "next/navigation";

const ProductList = () => {
  const router = useRouter();
  const [products, setProducts] = useState<Product[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(false);

  const fetchProducts = async () => {
    try {
      setIsLoading(true);
      const token = localStorage.getItem("token") || "";
      const res: ProductResponse = await GetAllProducts(token);
      if (res.con) {
        setProducts(res.result);
      } else {
        toast.error(res.message);
      }
    } catch (error: unknown) {
      const message =
        error instanceof Error ? error.message : "Failed to fetch products";
      toast.error(message);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    fetchProducts();
  }, []);

  return (
    <div className="p-4">
      {/* uppder division */}
      <div className="flex justify-between mt-2">
        <h1 className="text-3xl font-semibold">Products</h1>
        <div className="flex items-center gap-4">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400 w-5 h-5" />
            <input
              type="text"
              placeholder="Search by name, category"
              className="pl-10 pr-4 py-2 rounded-lg shadow-lg border border-gray-200 
                     focus:outline-none focus:ring-2 focus:ring-blue-500 transition duration-200"
            />
          </div>
          <button
            className="px-4 py-2 rounded-lg bg-blue-500 shadow-lg text-white font-semibold hover:bg-blue-600 hover:scale-105 
             transition duration-300 ease-in-out"
            onClick={() => router.push("/dashboard/product/create")}
          >
            + Add New Product
          </button>
        </div>
      </div>
      <BreadCrumb items={[{ label: "Product" }]} />
      {/* Product List table */}
      <div className="overflow-x-auto rounded-lg border border-gray-200 shadow-md bg-white mt-4">
        <table className="w-full text-sm text-left text-gray-700">
          <thead className="text-xs uppercase bg-gray-100 text-gray-600 border-b">
            <tr>
              <th scope="col" className="px-6 py-3 font-medium">
                #
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Name
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Brand
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Type
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Category
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Capacity
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Price
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Quantity
              </th>
              <th scope="col" className="px-6 py-3 font-medium">
                Action
              </th>
            </tr>
          </thead>
          <tbody>
            {isLoading ? (
              <ProductListSkeleton />
            ) : products.length === 0 ? (
              <ProductListEmpty />
            ) : (
              products.map((product, index) => {
                return (
                  <ProductRow key={index} product={product} index={index + 1} />
                );
              })
            )}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ProductList;
